:root {
    --chrome-content-separator-color: #6f6f6f !important;
    
    --urlbar-width: 28.83%;
    --urlbar-min-width: 198px;
    --urlbar-height: 24.00px !important;
    --urlbar-min-height: 24px !important;

    @media (-moz-windows-classic) {
        --urlbar-height: 22.00px !important;
        --urlbar-min-height: 22px !important;
    }

    --urlbarView-result-button-size: 16px !important;
    --tab-block-margin: 0 !important;
    --toolbarbutton-hover-background: transparent !important;
    --toolbarbutton-active-background: transparent !important;
    --toolbarbutton-disabled-opacity: 1 !important;

    --urlbar-inprivatebadge-width: 58px;

    --urlbar-box-hover-bgcolor: transparent !important;
    --urlbar-box-active-bgcolor: transparent !important;
    
    --urlbar-dropdown-button_width: 32px;
    --urlbar-fake-dropdown-mask: url(images/search-dropdown-mask.svg);

    --urlbar-fake-dropdown-mask-position: center;
    --stop-reload-button_width: calc(10px + 6px);

    @media (-moz-windows-classic) {
        --stop-reload-button_width: 22px;
    }

    --focus-outline-color: transparent !important;

    --browser-outer-border-color: rgba(253, 253, 253, .65);
    --browser-inner-border-color: rgba(24, 24, 24, .57);

    --tooltip_background: linear-gradient(0deg, rgb(228, 229, 240) 0%, rgb(255, 255, 255) 100%);
    --tootlip_text-colour: rgb(87, 87, 87);
    --tooltip_border-colour: rgb(118, 118, 118);

    --pop-up_shadow: 2px 2px 2px 0px rgba(0, 0, 0, .5);

    --toolbaritems-margin-bottom: 3px;

    --toolbarbutton-blue-colour_hover: rgb(3, 132, 206);

    --toolbarbutton-icon-fill-attention: var(--toolbarbutton-blue-colour_hover) !important;

    --toolbar-field-background-color: rgba(254, 254, 254, .65) !important;
    --toolbar-field-background-color_hover: rgba(255, 255, 255, .8);

    @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
        --toolbaritems-margin-bottom: 4px;
        --urlbar-width: 28.25%;
        --urlbar-dropdown-button_width: 35px;
        --stop-reload-button_width: 23px;

        --urlbar-inprivatebadge-width: 75px;
        --urlbar-fake-dropdown-mask-position: left;

        @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease7777") {
            --stop-reload-button_width: 25px;
            --urlbar-fake-dropdown-mask: url(images/search7777-dropdown-mask.svg);
        }
    }
	
	&[customcolour] {
		--desiredColourForUI: #0284c5;
	}

    &:is([privatebrowsingmode="temporary"]) {
        #urlbar-input-container {
            &::before {
                content: '';
                height: 100%;
                width: var(--urlbar-inprivatebadge-width);
                background: url(images/InPrivateBadge9.svg);
                background-position: center !important;
                background-repeat: no-repeat !important;

                @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
                    background: url(images/InPrivateBadge9PreRelease.svg);
                    margin-left: 2px;
                }
            }
        }
    }
}

toolbar:not([nowindowdrag="true"], [customizing="true"]) {
    -moz-window-dragging: no-drag !important;
}

@media (-moz-windows-classic) {
    :root[tabsintitlebar][sizemode="normal"] #toolbar-menubar {
        margin-top: 0px !important;
    }
}


#main-window {
    &[sizemode="maximized"],
    &[sizemode="fullscreen"] {
        #endToolbar {
            padding-right: 3px !important;
        }

        #PersonalToolbar,
        #toolbar-menubar,
		#personalCommandToolbar {
            border-left: none !important;
            border-right: none !important;

            &::before {
                width: 100% !important;
                left: -1px !important;
            }

            &::after {
                border-left: none !important;
                border-right: none !important;
            }
        }

        #toolbar-menubar {
            @media not (-moz-windows-classic) {
                padding: 0px 3px 1px 3px !important;
            }
        }

        #browser,
        #customization-container {
            border-left: none !important;
            border-right: none !important;
            border-bottom: none !important;
            border-radius: 0 !important;
            padding: 0 !important;

            &::before {
                content: unset !important;
            }
        }

        #navigator-toolbox {
            &::after {
                width: 100% !important;
                left: 0 !important;
            }
        }

        .notificationbox-stack {
            bottom: -3px !important;
        }
    }

    &[sizemode="maximized"] {
        #fakeTitlebarSpace {
            margin-bottom: -3px;
        }

        #toolbar-menubar {
            border-radius: 0 !important;

            &::after {
                border-radius: 0 !important;
                width: 100% !important;
            }
        }
    }

    &[sizemode="fullscreen"],
    &:not([tabsintitlebar="true"]) {
        #fakeTitlebarSpace {
            display: none !important;
        }
    }

    &[sizemode="fullscreen"] {
        background: black !important;
    }
}

#custom-identity-label {
	display: none;
}

#identity-icon {
	list-style-image: none !important;
}

#nav-bar-customization-target {
    align-items: end !important;

	&[securestate="ev"] {
		--toolbar-field-background-color: rgba(143, 253, 127, 0.4);
		--toolbar-field-background-color_hover: rgba(143, 253, 127, 0.6);
		--toolbar-field-border: rgba(150, 179, 147, 0.3);

		#urlbar-background, #searchbar {
			&:before {
				border: 1px solid var(--toolbar-field-border);
			}
		}

		#identity-box {
			margin-left: 6px !important;
		}

		#identity-icon-box {
			width: unset !important;

			&:active {
				transform: translate(1px, 1px);
			}

			#custom-identity-label {
				display: block;
			}
		}

		#stop-reload-button {
			&::before {
				content: "";
			}
		}
	}

	&[securestate="insecure"] {
		--toolbar-field-background-color: rgba(255, 71, 97, 0.59);
		--toolbar-field-background-color_hover: rgba(255, 143, 164, 0.61);
		--toolbar-field-border: var(--toolbar-field-background-color);

		#urlbar-background, #searchbar {
			&:before {
				border: 1px solid var(--toolbar-field-border);
			}
		}

		#stop-reload-button {
			&::before {
				content: "";
			}
		}

		#identity-icon {
            list-style-image: url(images/cert-error.svg) !important;
			mask-image: url(images/cert-error.svg) !important;
			mask-size: contain !important;
        }

		#identity-icon-box {
			width: unset !important;

			&:active {
				transform: translate(1px, 1px);
			}

			#custom-identity-label {
				display: block;
			}
		}
	}
}

#navigator-toolbox {
    border-bottom: none !important;
    position: relative;

    &::after {
        position: absolute;
		content: "";
		width: calc(100% - 4px);
		left: 2px;
		bottom: 0;
		height: 1px;
		border-bottom: 1px solid var(--chrome-content-separator-color);
		z-index: 1;

        @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
            border-bottom: 0;
        }
    }

    &:-moz-lwtheme {
        background: none !important;
        border-top: none !important;
    }
}

#titlebar {
    order: 2;
    flex-direction: column-reverse;
    justify-content: start;
    appearance: none !important;

    @media (-moz-windows-classic) {
        position: relative;
        border-top: 1px solid ThreeDShadow;
        border-bottom: 1px solid ThreeDShadow;

        &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            background-clip: border-box;
            border-top: 1px solid ThreeDHighlight;
            border-bottom: 1px solid ThreeDHighlight;
        }
    }
}

#toolbar-menubar[autohide="true"][inactive="true"]:not([customizing="true"]) {
    height: 0 !important;

    @supports not -moz-bool-pref(BeautyFox.option.bHideFakeInnerBorders) {
        @supports not -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
            padding-top: 1px !important;
        }
    }
}

#toolbar-menubar {
    border-top: 1px solid var(--browser-outer-border-color);
    border-left: 1px solid var(--browser-outer-border-color);
    border-right: 1px solid var(--browser-outer-border-color);
    padding: 0px 3px 1px 3px;
    height: 22px !important;
    color: black !important;
    position: relative;
    border-radius: 3px 3px 0 0;
    background-color: var(--toolbar-bgcolor) !important;
    background-clip: padding-box !important;
    border-inline-style: solid !important;
    overflow: hidden !important;

    @media (-moz-windows-classic) {
        padding: 2px 0px 2px 0px;
    }

    @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
        border-top: 0;
        border-radius: 0 !important;
    }

    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: calc(100% - 2px);
        height: 100%;
        border-top: 1px solid var(--browser-inner-border-color) !important;
        border-left: 1px solid var(--browser-inner-border-color) !important;
        border-right: 1px solid var(--browser-inner-border-color) !important;
        pointer-events: none;
        border-radius: 2px 2px 0 0;
        z-index: 1;

        @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
            border-top: 0 !important;
            border-radius: 0 !important;
        }
    }

    @media (-moz-windows-classic) {
        background-color: unset !important;
    }

    &[inactive="true"] {
        max-height: 2px !important;

        #menubar-items {
            height: 0 !important;
            overflow: hidden;
        }
    }

    &:not(:-moz-lwtheme):-moz-window-inactive {
        color: ThreeDShadow !important;
    }

    >spacer {
        display: none !important;
    }

    &[autohide="true"] {
        max-height: 0px !important;

        #menubar-items {
            &::after {
                content: unset !important;
            }
        }

        &:not([inactive="true"]) {
            max-height: unset !important;

            #menubar-items {
                &::after {
                    content: '' !important;
                }
            }
        }
    }
}

#main-menubar {
    height: 100%;
    background: transparent !important;
    text-shadow: none !important;

    >menu {
        padding: 0 2px;

        @media (-moz-windows-classic) {
            padding: 0 1px;
            border-top: 1px solid transparent !important;
            border-left: 1px solid transparent !important;
            border-bottom: 1px solid transparent !important;
            border-right: 1px solid transparent !important;
        }

        &[_moz-menuactive="true"] {
            color: white !important;

            @media (-moz-windows-classic) {
                background-color: -moz-Dialog !important;
                color: -moz-DialogText !important;

                border-top: 1px solid ThreeDHighlight !important;
                border-left: 1px solid ThreeDHighlight !important;
                border-bottom: 1px solid ThreeDShadow !important;
                border-right: 1px solid ThreeDShadow !important;
            }
        }

        &[open="true"] {
            @media (-moz-windows-classic) {
                border-top: 1px solid ThreeDShadow  !important;
                border-left: 1px solid ThreeDShadow  !important;
                border-bottom: 1px solid ThreeDHighlight !important;
                border-right: 1px solid ThreeDHighlight !important;

                >.menubar-text {
                    transform: translate(1px, 1px);
                }
            }
        }
    }
}

#menubar-items {
    margin-top: 0 !important;

    @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
        &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            height: 1px;
            width: 100vw;
            background-color: rgb(145, 150, 162);
        }
    }
}


.titlebar-spacer {
    display: none;
}

#fakeTitlebarSpace {
    appearance: -moz-window-titlebar;
    -moz-window-dragging: drag !important;
    display: flex;
    justify-content: end;
    align-items: end;

    @media (-moz-windows-classic) {
        align-items: center;
    }

    @media not (-moz-windows-compositor: 0) {
        margin-bottom: -11px;
    }

    @media (-moz-windows-compositor: 0) {
        margin-bottom: -4px;
    }

    .titlebar-buttonbox-container {
        transform: translateY(-1px) !important;
        
        &:first-of-type {
            display: none !important;
        }

        @media (-moz-windows-compositor: 1) {
            .titlebar-buttonbox {
                transform: translateY(-4px) !important;
            }
        }

        @media (-moz-platform: macos) {
            position: fixed !important;
            top: 6px !important;
            left: 0 !important;
            width: 0 !important;
        }
    }
}

#nav-bar {
    background-color: transparent !important;
    -moz-window-dragging: drag !important;
    flex: 1 !important;
    box-shadow: none !important;

	&[iscontainer] {
		--navButtons-colour: var(--containerColour) !important;
		--navButtons-disabled-colour: var(--containerColour) !important;
		margin-left: 28px !important
	}

    @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
        overflow: visible !important;
    }
}
:root[tabsOnNavRow] {
    &:not([chromehidden~="toolbar"]) {
		#nav-bar {
			flex: unset !important;
		}
	}
}

toolbarpaletteitem[id*="wrapper-customizableui-special-spring"] {
    height: 28px !important;

    >toolbarspring {
        margin-block: 0 !important;
    }
}

#urlbar-container {
    padding-right: 0px;
    margin-inline-start: 1px !important;
    margin-inline-end: 0px !important;
    z-index: 4 !important;
    --urlbar-container-height: 24px !important;

    @media not (-moz-windows-classic) {
        &:hover, 
        &.toolbar-hover-fix {
            #urlbar-background {
                &::before {
                    background-color: var(--toolbar-field-background-color_hover) !important;
                }
            }
        }
    }
}

#stop-reload-button {
    justify-content: end;
    z-index: 3;
    height: 24px !important;
	position: relative;

	&::before {
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		box-sizing: border-box;
		border: 1px solid var(--toolbar-field-border);
	}

    @media not (-moz-windows-classic) {
        &:hover {
            background-color: var(--toolbar-field-background-color_hover) !important;
        }
    
        &.toolbar-hover-fix {
            background-color: var(--toolbar-field-background-color_hover) !important;
        }
    
        &[urlbarfocus="true"] {
            background-color: white !important;
        }
    }

    >toolbarbutton {
        list-style-image: none !important;
        position: relative;

        @media (-moz-windows-classic) {
            background: rgb(244,246,252);
            background: linear-gradient(0deg, rgba(244,246,252,0.65) 0%, rgba(229,234,248,0.65) 49%, rgba(239,242,251,0.65) 50%, rgba(244,245,246,0.65) 100%);
            border: 1px solid rgba(138,144,158,.65) !important;
            border-radius: 3px !important;
            outline-offset: -2px;
            outline: 1px solid rgba(255,255,255,.5);

            &:hover {
                background: linear-gradient(0deg, #e7eaf1 0%, #c1c8dc 49%, #dde4f7 50%, #f4f5f6 100%);
                border: 1px solid #8a909e !important;
            }

            &:active {
                background: linear-gradient(0deg, #c1ccd4 0%, #9eafba 49%, #c5d0db 50%, #e6f0f9 100%);
                border: 1px solid rgba(84,96,123,.8) !important;
                outline: 1px solid rgba(0,0,0,.2);
            }
        }

        &::before {
            content: '';
            mask-position: center !important;
            mask-repeat: no-repeat !important;
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: 100%;
            transition: background-color .3s steps(5);

            @media not (-moz-windows-classic) {
                background-color: rgba(0, 0, 0, .5);
            }
        }

        &:active {
            &::before {
                transition: none;
            }
        }
    }

    #reload-button {
        @media (-moz-windows-classic) {
            margin-right: 2px !important;
            
            &::before {
                background-image: url(chrome://userchrome/content/resources/ieframe.dll/Bitmap/ADDRESS_XP-ADDRESS_XP_120_03.svg);
                background-position: center;
                background-repeat: no-repeat;
                background-size: 16px;
            }
        }

        @media not (-moz-windows-classic) {
            &::before {
                mask: url(chrome://userchrome/content/images/refresh-mask.svg);
            }
    
            &:hover {
                &::before {
                    background-color: rgb(0, 161, 220);
                }
            }
    
            &:active {
                &::before {
                    background-color: rgb(3, 132, 206);
                }
            }
        }

        &.reload-button-display-none {
            opacity: 0 !important;
            pointer-events: none !important;
        }
    }

    #stop-button {
        @media (-moz-windows-classic) {
            margin-left: 2px !important;

            &::before {
                background-image: url(chrome://userchrome/content/resources/ieframe.dll/Bitmap/ADDRESS_XP-ADDRESS_XP_120_02.svg);
                background-position: center;
                background-repeat: no-repeat;
                background-size: 16px;
            }
        }
        @media not (-moz-windows-classic) {
            &::before {
                mask: url(chrome://userchrome/content/images/stop-mask.svg);
            }
    
            &:hover {
                &::before {
                    background-color: #E73C3C;
                }
            }
    
            &:active {
                &::before {
                    background-color: #E10C0C;
                }
            }
        }
    }

    +toolbarbutton {
        margin-left: 8px !important;
    }
}

#identity-box[pageproxystate="valid"].localResource>#identity-icon-box {
    position: relative;

    @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
        width: var(--stop-reload-button_width) !important;
        border-left: 1px solid rgba(0, 0, 0, 0.6) !important;
    }

    &::before {
        content: '';
        mask-position: center !important;
        mask-repeat: no-repeat !important;
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, .5);
        transition: background-color .3s steps(5);
    }

    &:active {
        &::before {
            transition: none;
            background-color: rgb(3, 132, 206);
        }
    }

    &::before {
        mask: url(chrome://userchrome/content/images/storedpage-mask.svg);
    }

    &:hover {
        &::before {
            background-color: rgb(0, 161, 220);
        }
    }

    image {
        display: none !important;
    }
}

#urlbar {
    font-size: 9pt !important;
    top: 0 !important;
    max-height: var(--urlbar-height) !important;

    @media (-moz-windows-classic) {
        font-size: 8pt !important;
    }

    &[focused=true] {
        #urlbar-background {
            box-shadow: none !important;
        }
    }

    &[breakout] {
        &[breakout-extend] {
            left: 0 !important;
            width: 100% !important;

            >#urlbar-input-container {
                height: var(--urlbar-height) !important;
            }
        }
    }
}

#urlbar-input-container {
    padding: 0 0 0 0 !important;
    border-radius: 0 !important;
    border-right: 0 !important;

    &.urlbar-remove-padding-right {
        overflow: visible !important;
        margin-inline-end: -16px !important;
        width: auto !important;

        @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
            margin-inline-end: calc(var(--stop-reload-button_width) * -1) !important;
        }
    }
}

.urlbar-input-box {
    padding: 0 var(--urlbar-dropdown-button_width) 0 0;
    position: relative;
    margin-left: 5px !important;

    &::after {
        content: '';
        transform: translateX(var(--urlbar-dropdown-button_width));
        width: var(--urlbar-dropdown-button_width);
        height: 22px;
        background: linear-gradient(90deg, rgba(0, 0, 0, .5) 49%, rgba(0, 0, 0, .5) 50%);
        mask: var(--urlbar-fake-dropdown-mask);
        mask-position: var(--urlbar-fake-dropdown-mask-position);
        mask-repeat: no-repeat;
        margin-right: 2px;

        @supports -moz-bool-pref("BeautyFox.appearance.IE9PreRelease") {
            background: linear-gradient(90deg, rgba(0, 0, 0, .1) 49%, rgba(0, 0, 0, .5) 50%);
        }
    }
}

:root:not([tabsOnNavRow]) {
    .urlbar-input-box {
        &::after {
            margin-left: calc(var(--urlbar-dropdown-button_width) / 2 * -1);
        }
    }

	#TabsToolbar {
		margin-right: 2px;
	}
}

#urlbar-input {
    margin-right: -31px !important;
    mask-image: none !important;
    overflow-x: hidden;
    text-overflow: ellipsis;
}

#favimginurlbar {
    width: 16px;
    height: 16px;
    margin-left: 3px;
    margin-top: auto;
    margin-bottom: auto;
}

#personalCommandToolbar {
	order: 3;
	//background-color: var(--toolbar-bgcolor) !important;
	border-left: 1px solid var(--browser-outer-border-color);
    border-right: 1px solid var(--browser-outer-border-color);
	position: relative;

	> .browser-toolbar {
		z-index: 1 !important;
	}

	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: -moz-available;
		height: 100%;
		background-color: var(--toolbar-bgcolor);
		box-sizing: border-box;
		margin-inline: 1px;
	}

	&::after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-left: 1px solid var(--browser-inner-border-color) !important;
        border-right: 1px solid var(--browser-inner-border-color) !important;
        pointer-events: none;
        box-sizing: border-box;
    }
}

#PersonalToolbar {
    height: 30px;
    padding-inline: 3px !important;
    //background-color: var(--toolbar-bgcolor) !important;
	background-color: transparent !important;
    padding: 0 1px;
	flex: 1;
    padding-right: 1px !important;

    @media (-moz-windows-classic) {
        background-color: unset !important;
        padding-inline: 0px !important;
    }    

    .bookmark-item[dragover="true"] {
        color: black !important;
    }

    .bookmark-item[container] {
        list-style-image: url("moz-icon:file:///C:/ProgramData/?size=16") !important;
    }

    .bookmark-item:not(menuitem, .menu-iconic),
    #star-button-box {
        margin: 2px 0 3px 0 !important;
        padding: 3px !important;
        max-width: 175px !important;
        border: 1px solid transparent;
        position: relative;
        appearance: auto !important;

        &:active,
        &[open="true"] {

            .toolbarbutton-icon,
